
#app {
  height: 100%;
  width: 100%;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: whitesmoke;
  margin-top: 60px;
}

.reserve-button{
    font: 700 1.2rem 'Roboto Slab', sans-serif;
    font-size: 20px;
    color: #ffe593;
    line-height: 1.5;
    padding: .5em 1.5em;
    background: none;
    border: 2px solid #ffe593;
    outline: none;
}


.reserve-app {
    width: 100%;
    height: 100%;
    background-color: red;
}
